/* nav-bar */
.nav-bar-cont {
  width: 100%;
  position: fixed;
  z-index: 66;
  height: 0.4rem;
  background: #fff;
  border-bottom: 1px solid #d5d5d5;
  top: 0;
}
.nav-bar-cont .button {
    width: 15%;
    height: 0.4rem;
    display: block;
    text-align: center;
}
.nav-bar-cont .title {
    width: 70%;
    height: 0.4rem;
    text-align: center;
}
.nav-bar-cont i {
    cursor: pointer;
}
.logo {
  display: inline-block;
  background: url("../../assets/images/logo.png") no-repeat;
  background-size: 100%;
  width: 0.83rem;
  height: 0.22rem;
  margin-top: 0.1rem;
}
.icon-list {
  display: inline-block;
  background: url("../../assets/images/icon-list.png") no-repeat;
  background-size: 100%;
  width: 0.17rem;
  height: 0.17rem;
  margin-top: 0.12rem;
}
.icon-search {
  display: inline-block;
  background: url("../../assets/images/icon-search.png") no-repeat;
  background-size: 100%;
  width: 0.17rem;
  height: 0.17rem;
  margin-top: 0.12rem;
}
.icon-back {
  display: inline-block;
  background: url("../../assets/images/icon-back.png") no-repeat;
  background-size: 100%;
  width: 0.17rem;
  height: 0.17rem;
  margin-top: 0.12rem;
}

/* layout */
@media screen and (min-width: 960px) {
.nav-bar-cont {
    width: 640px;
}
}



